// 4) Хук для доступа к текущему языку и переводчику
export function useLang() {
  const ctx = useContext(LanguageContext);
  if (!ctx) {
    throw new Error("useLang must be used within a LanguageProvider");
  }
  return ctx; // { lang, changeLang, t }
}
