import React, { createContext, useState, useEffect, useContext } from "react";
import axios from "axios";

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [authData, setAuthData] = useState(null); // null — ещё не знаем, false — точно не вошёл
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios
      .get("/api/login", { withCredentials: true })
      .then((res) => {
        setAuthData(res.data); // юзер авторизован
      })
      .catch(() => {
        setAuthData(false); // не авторизован
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  return (
    <AuthContext.Provider value={{ authData, setAuthData, loading }}>
      {children}
    </AuthContext.Provider>
  );
};
